%chart-1 {
  width: 70px;
  height: 70px;
  margin-left: 15px;
  margin-top: 15px;
  line-height: 70px;
}
%chart-2 {
  width: 35px;
  height: 35px;
  margin-left: 7.5px;
  margin-top: 7.5px;
  line-height: 35px;
}
%chart-3 {
  width: 20px;
  height: 20px;
  margin-left: 5.5px;
  margin-top: 4.5px;
  line-height: 21px;
}


.chart-block {
  canvas {
    width: 100% !important;
  }
  .chart-overflow {
    overflow: hidden;
    text-align: center;
  }
}
.candidcahrt {
  width: 100%;
  height: 470px;
}
.amcharts-export-canvas {
  position: absolute;
  display: none;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $white;
  .canvas-container {
    transition: opacity .5s ease-out;
  }
  &.active {
    display: block;
  }
  &.dropbox .canvas-container {
    opacity: .5;
  }
}
.flot-chart-container {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;
  height: 400px;
  #toggling-series-flot {
    width: 79% !important;
  }
}
.flot-chart-placeholder {
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 1.2em;
  text-align: center;
  .legend table {
    border-spacing: 5px;
  }
}
.morris-hover {
  position: absolute;
  z-index: 8;
  &.morris-default-style {
    border-radius: 10px;
    padding: 6px;
    color: $dark-gray;
    background: $white;
    border: solid 1px $light-gray;
    font-family: $font-serif;
    font-size: 12px;
    text-align: center;
    .morris-hover-row-label {
      font-weight: bold;
      margin: 0.25em 0;
    }
    .morris-hover-point {
      white-space: nowrap;
      margin: 0.1em 0;
    }
  }
}
.chart-sparkline {
  position: absolute;
  left: 0;
  top: 0;
  visibility: hidden;
  background: rgba(49, 49, 49, 0.70);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
  color: $white;
  font: 10px $font-work-sans, $font-serif;
  text-align: left;
  white-space: nowrap;
  padding: 5px;
  border: 1px solid rgba(49, 49, 49, 0.75);;
  box-sizing: content-box;
  z-index: 10000;
  border-radius: 5px;
  .jqsfield {
    color: $white;
    font: 10px $font-work-sans, $font-serif;
    text-align: left;
  }
}
.pieLabel{
  div {
    color: $white !important;
    input[type=checkbox] {
      margin: 7px;
    }
  }
}
// knob chart css
.knob-chart{
  min-height: 255px;
  .chart-clock-main{
    position:relative;
    width:200px;
    margin:45px auto;
    .clock-large{
      position:absolute;
      left:-48px;
      top:-45px;
    }
    .clock-medium{
      position:absolute;
      left:2px;
      top:5px;
    }
    .clock-small{
      position:absolute;
      left:15px;
      top:55px;
    }
  }
}
// peity chart css
.peity-charts {
  svg {
    width: 100%;
    height: 250px;
    filter: drop-shadow(12px 12px 7px rgba(211, 211, 211, 0.3));
  }
  &.peity-chart-primary{
    svg{
      polygon{
        fill: rgba($primary-color, 0.5);
      }
      polyline{
        stroke: $primary-color;
      }
    }
  }
  .bar {
    ~ svg {
      rect {
        &:nth-child(odd) {
          fill: $primary-color;
        }
        &:nth-child(even) {
          fill: $secondary-color;
        }
      }
    }
  }
  .donut, .pie  {
    ~ svg {
      path {
        &:nth-child(odd) {
          fill: $primary-color;
        }
        &:nth-child(even) {
          fill: $light-semi-gray;
        }
      }
    }
  }
}
.data-attributes {
  span {
    ~ svg {
      path {
        &:last-child {
          fill: $light-semi-gray;
        }
      }
    }
  }
  .small-donut {
    ~ svg {
      path {
        &:first-child {
          fill: $primary-color;
        }
      }
    }
  }
  .small-donut1 {
    ~ svg {
      path {
        &:first-child {
          fill: $secondary-color;
        }
      }
    }
  }
  .small-donut2 {
    ~ svg {
      path {
        &:first-child {
          fill: $success-color;
        }
      }
    }
  }
  .small-donut3 {
    ~ svg {
      path {
        &:first-child {
          fill: $info-color;
        }
      }
    }
  }
  .small-donut4 {
    ~ svg {
      path {
        &:first-child {
          fill: $warning-color;
        }
      }
    }
  }
  .small-donut5 {
    ~ svg {
      path {
        &:first-child {
          fill: $danger-color;
        }
      }
    }
  }
  .small-donut6 {
    ~ svg {
      path {
        &:first-child {
          fill: $primary-color;
        }
      }
    }
  }
}
p{
  &.data-attributes{
    svg{
      margin-right: 10px;
    }
  }
}
// knob chart
.knob-block {
  position: relative;
  input{
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin-left: 0 !important;
    margin-top: -3px !important;
  }
}
// apex chart
.apex-chart {
  margin: 0 auto;
}
.apexcharts-menu-item {
  width: 110px;
}
.word-tree{
  height: 400px;
}
// chartist chart color
.ct-series-a {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $primary-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $primary-color;
  }
}
.ct-series-b {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $secondary-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $secondary-color;
  }
}
.ct-series-c {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $success-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $success-color;
  }
}
.ct-series-d {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $info-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $info-color;
  }
}
.ct-series-e {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $warning-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $warning-color;
  }
}
.ct-series-f {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $danger-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $danger-color;
  }
}
.ct-series-g {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $light-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $light-color;
  }
}
