/**=====================
    25. Form CSS Start
==========================**/
.height-35 {
  height: 35px !important;
}
.theme-form {
  label {
    font-size: 16px;
  }
  .form-control {
    :focus {
      box-shadow: 0 0 0 0.2rem rgba(171, 140, 228, 0.05);
    }
  }
  &.sm-form {
    .form-group {
      margin-bottom: $sm-form-margin-bottom;
    }
  }
  .form-group {
    margin-bottom: $form-group-margin-bottom;
    textarea {
      border-color: $form-input-border-color;
      border-radius: 20px;
      width: 100%;
      &::-webkit-input-placeholder {
        color: $form-placeholder-color;
        font-size: $form-placeholder-font-size;
        letter-spacing: $form-placeholder-letter-spacing;
        font-weight: $form-placeholder-font-weight;
      }
    }
    input[type=text],
    input[type=email],
    input[type=search],
    input[type=password],
    input[type=number],
    input[type=tel],
    input[type=date],
    input[type=datetime-local],
    input[type=time],
    input[type=datetime-local],
    input[type=month],
    input[type=week],
    input[type=url],
    input[type=file] {
      height: calc(2.2rem + 2px);
      padding-left: 20px;
    }
    input[type=text],
    input[type=email],
    input[type=search],
    input[type=password],
    input[type=number],
    input[type=tel],
    input[type=date],
    input[type=datetime-local],
    input[type=time],
    input[type=datetime-local],
    input[type=month],
    input[type=week],
    input[type=url],
    input[type=file],
    select {
      border-color: $form-input-border-color;
      background-color: $form-input-bg-color;
      font-size: $form-placeholder-font-size;
      color: $form-placeholder-color;
      font-family: $font-work-sans, $font-serif;
      border-radius: 20px;
      &::-webkit-input-placeholder {
        color: $form-placeholder-color;
        font-size: $form-placeholder-font-size;
        letter-spacing: $form-placeholder-letter-spacing;
        font-weight: $form-placeholder-font-weight;
      }
    }
    select.form-control:not([size]):not([multiple]) {
      border-color: $form-input-border-color;
      font-size: $form-placeholder-font-size;
    }
  }
  .checkbox {
    label {
      padding-left: 10px;
    }
  }
  .form-divider {
    position: relative;
    clear: both;
    height: 0;
    margin: 30px 0;
    border-top: 1px solid $light-semi-gray;
    &:before {
      position: absolute;
      top: -11px;
      left: 40%;
      margin-left: -10px;
      padding: 0 5px;
      content: "Or Signup With";
      color: $theme-font-color;
      background: $white;
    }
  }
  .login-divider {
    position: relative;
    clear: both;
    height: 0;
    margin: 30px 0;
    border-top: 1px solid $light-semi-gray;
    &:before {
      position: absolute;
      top: -11px;
      left: 40%;
      margin-left: -10px;
      padding: 0 5px;
      color: $theme-font-color;
      background: $white;
      content: "Or Login With";
    }
  }
  .btn-fb {
    background-color: $fb;
    color: $white;
    width: 100%;
  }
  .btn-twitter {
    background-color: $twitter;
    color: $white;
    width: 100%;
  }
  .btn-google {
    background-color: $google-plus;
    color: $white;
    width: 100%;
  }
}
input[type=file] {
  height: auto !important;
}
.form-inline {
  .form-group {
    margin-right: 15px;
    @media (min-width: 576px) {
      display: flex;
      flex: 0 0 auto;
      flex-flow: row wrap;
      align-items: center;
      margin-bottom: 0;
      width: unset;
    }
    .col-form-label {
      margin-right: 5px;
    }
  }
  label {
    @media (min-width: 576px) {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 0;
    }
  }
  .form-control {
    @media (min-width: 576px) {
      display: inline-block;
      width: auto;
      vertical-align: middle;
    }
  }
}
.bottom-minus {
  margin-bottom: -15px;
}
.search-form {
  position: relative;
  .form-group {
    &:before {
      position: absolute;
      content: "";
      width: 1px;
      height: 25px;
      background: #e8ebf2;
      left: 52px;
      top: 10px;
    }
    &:after {
      position: absolute;
      content: "\f002";
      font-family: FontAwesome;
      top: 11px;
      left: 25px;
      color: #8e8e8e;
    }
  }
  input {
    border: 1px solid $light-semi-gray;
    padding: 10px 10px 10px 70px;
    border-radius: 50px;
    background-color: rgba($primary-color, 0.02);
  }
}
.form-label-align-right {
  label {
    text-align: right;
    padding-top: 17px;
    font-weight: 500;
  }
}
.custom-file-label {
  line-height: 1.8;
  &::after {
    line-height: 1.8;
  }
}
/**=====================
    25. Form CSS Ends
==========================**/